.dino {
  padding-bottom: 36px;
  margin: 0 auto;
}

.dino-title {
  font-weight: 400;
  font-size: 16px;
  text-align: center;
  padding-top: 36px;
}
.dino-container {
  margin-top: 36px;
  width: 100%;
  height: 200px;
  position: relative;
  overflow: hidden;
  background-repeat: repeat-x;
  background-position-y: bottom;
  background-color: rgb(223, 223, 255);
}
.dino-results {
  color: white;
  font-size: 20px;
  text-align: center;
}
.dino-but {
  fill: white;
  color: white;
  background-color: transparent;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 140px;
  font-size: 20px;
  cursor: pointer;
  border: solid 2px;
  padding: 10px;
  border-radius: 15px;
  display: none;
}
.dino-overlay {
  width: 100%;
  height: 100%;
  z-index: 999;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  padding: 25px;
  gap: 20px;
  flex-direction: column;
  align-items: center;
}
.dino-points {
  right: 0;
  position: absolute;
}
.dino-player {
  width: 70px;
  height: 80px;
  transition: all 0.2s;
  position: absolute;
  bottom: 15px;
  left: 100px;
}
.dino-cactus {
  width: 50px;
  height: 70px;
  transition: all 0.2s;
  position: absolute;
  bottom: 15px;
  right: 0;
}
