.card-ten__title {
  font-weight: 400;
  font-size: 16px;
  color: #000;
  display: flex;
  justify-content: center;
  padding-top: 36px;
  margin-bottom: 36px;
}
.card-ten__item {
  background-color: #d9d9d9;
  border-radius: 20px;
  width: 100px;
  height: 100px;
}
.card-ten__btn {
  font-size: 12px;
  color: #fff;
  border-radius: 20px;
  padding: 10px 20px;
  background-color: #000;
  border: none;
  margin-bottom: 20px;
  font-family: 'Montserrat', sans-serif;
  display: flex;
  cursor: pointer;
}
.card-ten__text {
  font-size: 8px;
  line-height: 170%;
  text-align: center;
  color: #000;
  margin-top: 40px;
}
.card-ten__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-bottom: 36px;
  gap: 59px;
  width: 600px;
}
.card-ten__button {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}
.card-ten__button-one {
  display: flex;
  flex-direction: column;
  width: 400px;
  align-items: end;
}
.card-ten__button-two {
  display: flex;
  flex-direction: column;
  width: 420px;
  align-items: start;
  margin-left: 10px;
}
.card-ten__wrap {
  display: flex;
  justify-content: center;
}
#btn8 {
  width: 368px;
  text-align: start;
}
.card-ten__last-btn {
  margin: 0 auto;
}
